import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import idleDetail from '@/view/idle/idle-detail.vue'; //闲置首页

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'idleDetail',
    component: idleDetail
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



