/* eslint-disable */

import { isObject, urlencode } from './util'
import { isApp } from './env'


// 类型检查
function check (vms, _vmName, _data) {
  if (_vmName && !vms.hasOwnProperty(_vmName)) { throw new Error('没有发现' + _vmName) }
  if (_data && !isObject(_data)) { throw new Error(_vmName + ': 传入的$data应为一个对象') }
}

export default function (g) {
  if (!isApp) { return }
  if (g.hasOwnProperty('bridge')) {
    throw new Error('bridge字段被占用')
  } else {
    if (!g.vms) { throw new Error('没有发现vms字段') }
    let vms = g.vms
    if (!isObject(vms)) { throw new Error('vms类型错误') }

    g.bridge = {
      // 查看data
      getData (_vmName, isStringify) {
        _vmName = String(_vmName)
        check(vms, _vmName)
        let vm = vms[_vmName]
        return isStringify ? JSON.stringify(vm.$data, null, 4)
          : Object.assign({}, vm.$data)
      },
      // 推送数据到组件
      pushData (_vmName, _data) {
        _vmName = String(_vmName)
        check(vms, _vmName, _data)
        let vm = vms[_vmName]
        Object.assign(vm, _data)
      }





    }
  }
}


function execute (url) {
  let iframe = document.createElement('IFRAME')
  iframe.setAttribute('src', url)
  document.documentElement.appendChild(iframe)
  iframe.parentNode.removeChild(iframe)
  iframe = null
}

/* ======================================== 原生调用 ======================================== */

// 原生图片浏览
export function nativeBrowse (_imgUrl) {
  if (!isApp) { return }
  let temp = 'dgtle4://nativeBrowse?imgUrl=' + _imgUrl
  execute(temp); console.log(temp); console.log('图片浏览: ', _imgUrl)
}
// 跳转外链
export function nativeOpenBrowser (_url) {
  if (!isApp) { return }
  let temp = 'dgtle4://nativeOpenBrowser?url=' + _url
  execute(temp); console.log(temp); console.log('跳转外链: ', _url)
}
// 产品筛选的品牌筛选
export function nativeOpenProductBrandFilter () {
  if (!isApp) { return }
  let temp = 'dgtle4://nativeOpenProductBrandFilter'
  execute(temp); console.log(temp); console.log('产品筛选的品牌筛选: ')
}
/* ======================================== 交互调用 ======================================== */
export function nativeGo (_viewName, _params) { // _params == obj ; _viewName == 视口的文件名
  if (!isApp) { return }
  let temp = 'dgtle4://nativeGo?' + _viewName + '&' + urlencode(_params, false)
  execute(temp); console.log(temp); console.log('跳转: ', _viewName, _params)
}
export function nativeAddFavorites (_params) {
  if (!isApp) { return }
  let temp = 'dgtle4://nativeAddFavorites' + urlencode(_params)
  execute(temp); console.log(temp); console.log('加入收藏夹: ', _params)
}

  /* ---------------------------------------- 点评模块 ---------------------------------------- */
  export function nativeCommentLink (_params) {
    if (!isApp) { return }
    let temp = 'dgtle4://nativeCommentLink' + urlencode(_params)
    execute(temp); console.log(temp); console.log('点评点赞 或 取消: ', _params)
  }

  export function nativeCommentReviewLink (_params) {
    if (!isApp) { return }
    let temp ='dgtle4://nativeCommentReviewLink' + urlencode(_params)
    execute(temp); console.log(temp); console.log('点评评论点赞 或 取消: ', _params)
    //https://dev.api.dgtle.com/doc/restful_dgtle_yii_doc/#api-review-STORYCOMMENTLIKE
  }
  
  export function nativeCommentRankFavorites (_params,_isCancel) {
    if (!isApp) { return }
    if(_isCancel){//是否取消收藏
      let temp = 'dgtle4://nativeCommentRankCancelFavorites' + urlencode(_params)
      execute(temp); console.log(temp); console.log('榜单详情点取消收藏: ', _params)
    }else{
      let temp = 'dgtle4://nativeCommentRankFavorites' + urlencode(_params)
      execute(temp); console.log(temp); console.log('榜单详情点收藏: ', _params)
    }
  }

  // 产品搜索
  export function nativeCommentProductLibrarySearch (_params) { // _params.filter == brand tag screen 通过这个 filter 分辨现在是
    if (!isApp) { return }
    let temp = 'dgtle4://nativeCommentProductLibrarySearch' + urlencode(_params)
    execute(temp); console.log(temp); console.log('产品搜索: ', _params)
  }


